import React from 'react'
import Item from '../Item'
import Details from '../Details'

const Main = ({
  coinsData,
  getCoinDetails,
  isShowDetails,
  isHideCoinDetails,
  selectCoinID
}) => {

  let coinNumber = 1

  return (
    <React.Fragment>

      <style jsx>{`
          ul {
             list-style: none;
            background-color: white;
          }
        `}
      </style>


      {isShowDetails ?
        <div>
        
            {coinsData.length !== 0 ?
              coinsData.map((dataItem, key) => {
                const coinProps = {
                  coinNumber: coinNumber++,
                  getCoinDetails,
                  dataItem
                }
                return (
                  <table border="1">
                  <tbody>
               
                  <tr>
                   <th>coinNumber</th>
               
                   <th>ImageUrl</th>
                   <th>Name</th>
                   <th>FullName</th>
                   <th>PRICE</th>
                   <th>VOLUME24HOURTO</th>
                   <th>TOTALVOLUME24HTO</th>
                   <th>CHANGEPCT24HOUR</th>
                  </tr>
                  <React.Fragment key={key}>
                    <Item {...coinProps} />
                  </React.Fragment>
                  </tbody>
         </table>
                )
              })
              : <h3>No Coin</h3>
            }

        </div>
        :
        <div>
          <button onClick={() => isHideCoinDetails()}>ok</button>
          {coinsData.length !== 0 ?
            coinsData
              .filter(dataItem => {
                if (selectCoinID !== '') {
                  return dataItem.CoinInfo.Id === selectCoinID
                }

                return dataItem
              })
              .map((dataItem, key) => {
                return (
                  <Details key={key} dataItem={dataItem} />
                )
              })
            : <h3>No Coin</h3>
          }
        </div>
      }
    </React.Fragment>
  )
}

export default Main